.my-orders-container {
  padding: 100px 20px 40px;
  margin: 0 auto;
  min-height: 100vh;
  background-image: url('/public/beige-abstract-wallpaper-background-image.jpg');
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.my-orders-container h1 {
  text-align: center;
  margin-bottom: 30px;
  color: #615d57;
  font-size: 28px;
  font-weight: 700;
}

.no-orders {
  text-align: center;
  padding: 50px 0;
  background-color: #fefef6;
  border-radius: 12px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.no-orders p {
  font-size: 18px;
  color: #666;
}

/* Orders List */
.orders-list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 20px;
}


.order-card {
  cursor: pointer;
  background-color: #fff;
  border-radius: 12px;
  overflow: hidden;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.order-card:hover {
  transform: translateY(-3px);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.15);
}

.order-card-header {
  background-color: #E8D5B9;
  padding: 15px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.order-number {
  font-weight: 700;
  font-size: 16px;
  color: #333;
}

.order-date {
  color: #666;
  font-size: 14px;
}

.order-card-body {
  padding: 20px;
}

.order-mess, .order-items-count, .order-total-amount {
  margin-bottom: 10px;
  color: #333;
}

.order-total-amount {
  color: #63948B;
  font-weight: 600;
}

.order-card-footer {
  padding: 15px;
  text-align: center;
  border-top: 1px solid #eee;
  display: flex;
  justify-content: space-between;
}

.view-details-btn, .order-again-btn {
  background-color: #615d57;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 6px;
  cursor: pointer;
  font-size: 14px;
  transition: all 0.3s ease;
}

.view-details-btn:hover, .order-again-btn:hover {
  background-color: #4a4640;
  font-size: 16px;
}

.order-again-btn {
  background-color: #63948B;
}

.order-again-btn:hover {
  background-color: #4c7871;
}

/* Order Details Container */
.order-details-container {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  padding: 100px 20px 40px;
  min-height: 100vh;
  background-image: url('/public/beige-abstract-wallpaper-background-image.jpg');
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.order-details {
  background-color: #615d57;
  color: white;
  border-radius: 12px;
  padding: 25px 35px;
  width: 100%;
  max-width: 700px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.order-details-header {
  margin-bottom: 30px;
}

.order-details h2 {
  text-align: center;
  margin-bottom: 20px;
  font-size: 24px;
}

.order-info {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 15px;
  padding-bottom: 15px;
  border-bottom: 1px solid #8e8e8e;
}

.order-info p {
  margin: 5px 0;
}

/* Order Items Table */
.order-items-table {
  width: 100%;
  border-collapse: collapse;
  margin: 25px 0;
}

.order-items-table th, .order-items-table td {
  padding: 12px;
  text-align: left;
}

.order-items-table th {
  font-weight: bold;
  border-bottom: 1px solid #8e8e8e;
}

.order-items-table tr:last-child td {
  border-bottom: 1px solid #8e8e8e;
}

/* Order Total */
.order-total {
  display: flex;
  justify-content: space-between;
  font-weight: bold;
  font-size: 18px;
  margin: 20px 0;
  padding-top: 10px;
}

/* Payment Method */
.order-payment-method {
  display: flex;
  justify-content: space-between;
  margin-bottom: 30px;
  font-size: 16px;
}

/* Actions Container */
.order-actions {
  display: flex;
  justify-content: space-between;
  gap: 20px;
}

/* Back to List Button */
.back-to-list-btn {
  display: block;
  padding: 12px 25px;
  background-color: #E8D5B9;
  color: #333;
  border: none;
  border-radius: 8px;
  font-size: 16px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.back-to-list-btn:hover {
  background-color: #d7c4a8;
  font-size: 1.28em;
}

/* Mess Selection Container */
.mess-selection-container {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 100px 20px 40px;
  min-height: 100vh;
  background-image: url('/public/beige-abstract-wallpaper-background-image.jpg');
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.mess-selection {
  background-color: #615d57;
  color: white;
  border-radius: 12px;
  padding: 30px;
  width: 100%;
  max-width: 600px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  text-align: center;
}

.mess-selection h2 {
  margin-bottom: 20px;
  font-size: 24px;
}

.mess-selection p {
  margin-bottom: 25px;
  font-size: 16px;
}

.mess-buttons {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
  margin-bottom: 30px;
}

.mess-buttons button {
  background-color: #E8D5B9;
  color: #333;
  border: none;
  padding: 15px;
  border-radius: 8px;
  font-size: 18px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
}

.mess-buttons button:hover {
  background-color: #d7c4a8;
  transform: translateY(-3px);
}

/* Media Queries */
@media (max-width: 768px) {
  .orders-list {
    grid-template-columns: 1fr;
  }
  
  .order-info {
    grid-template-columns: 1fr;
  }
  
  .order-actions {
    flex-direction: column;
  }
  
  .mess-buttons {
    grid-template-columns: 1fr;
  }
  
  .order-card-footer {
    flex-direction: column;
    gap: 10px;
  }
}