@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap');

.popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s ease, visibility 0.3s ease;
}

.popup.show {
  opacity: 1;
  visibility: visible;
}

.popup-content {
  background-color: white;
  border-radius: 12px;
  padding: 2rem;
  text-align: center;
  max-width: 400px;
  width: 90%;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  position: relative;
}

.popup-icon {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto 1rem;
  position: relative;
}

.popup-icon.success {
  background-color: #4CAF50;
}

.popup-icon.error {
  background-color: #F44336;
}

.popup-icon.success::before,
.popup-icon.success::after {
  content: '';
  position: absolute;
  background-color: white;
}

.popup-icon.success::before {
  width: 3px;
  height: 40px;
  left: 56%;
  top: 35%;
  transform: translateX(-50%) rotate(45deg);
}

.popup-icon.success::after {
  width: 3px;
  height: 20px;
  left: 35%;
  top: 52%;
  transform: translateX(-50%) rotate(-45deg);
}

.popup-icon.error::before,
.popup-icon.error::after {
  content: '';
  position: absolute;
  background-color: white;
  height: 3px;
  width: 40px;
  top: 50%;
  left: 50%;
}

.popup-icon.error::before {
  transform: translate(-50%, -50%) rotate(45deg);
}

.popup-icon.error::after {
  transform: translate(-50%, -50%) rotate(-45deg);
}

.popup-close {
  position: absolute;
  top: 10px;
  right: 10px;
  background: none;
  border: none;
  font-size: 1.5rem;
  cursor: pointer;
  color: #888;
}

.popup-message {
  margin-top: 1rem;
  font-size: 1rem;
  color: #333;
}


body 
{
  font-family: 'Poppins', sans-serif;
  margin: 0;
  padding: 0;
}

.auth-container 
{
  min-height: 100vh;
  background-color: #ddfffe;
  display: grid;
  place-content: center;
  font-family: 'Poppins', sans-serif;
  position: relative;
}

.overlay 
{
  position: absolute;
  inset: 0;
  background-color: black;
  opacity: 0.5;
  z-index: 0;
}

.auth-card 
{
  position: relative;
  width: 850px;
  height: 500px;
  background-color: white;
  box-shadow: 25px 30px 55px rgba(85, 85, 85, 0.4);
  border-radius: 13px;
  overflow: hidden;
  z-index: 10;
}

.signup-form-container 
{
  position: absolute;
  width: 60%;
  height: 100%;
  padding: 0;
  transition: all 0.6s ease-in-out;
  opacity: 0;
  z-index: 1;
}

.signup-form-container.right-panel-active 
{
  transform: translateX(66.7%);
  opacity: 1;
  z-index: 5;
}

.signin-form-container 
{
  position: absolute;
  width: 60%;
  height: 100%;
  padding: 0;
  transition: all 0.6s ease-in-out;
  z-index: 2;
}

.signin-form-container.hidden 
{
  opacity: 0;
  z-index: 0;
  pointer-events: none;
}

.signup-form, .signin-form 
{
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 0 70px;
  box-sizing: border-box;
}

.form-title 
{
  color: #1a1a1a;
  font-size: 1.5rem;
  font-weight: 700;
  margin-bottom: 2rem;
  margin-top: 0;
  width: 100%;
  text-align: center;
}

.input-field 
{
  position: relative;
  width: 100%;
  margin-bottom: 1rem;
}

.input 
{
  width: 100%;
  padding: 0.75rem 1rem;
  background-color: #f3f3ff;
  border: none;
  outline: none;
  border-radius: 0.25rem;
  transition: all 0.3s;
  box-sizing: border-box;
}

.input:focus 
{
  box-shadow: 0 0 0 2px #1a1a1a;
}

.submit-button 
{
  margin-top: 1.5rem;
  padding: 0.75rem 2rem;
  background-color: #1a1a1a;
  color: white;
  border: none;
  border-radius: 0.25rem;
  text-transform: uppercase;
  font-weight: 600;
  letter-spacing: 0.05em;
  transition: all 0.3s;
  cursor: pointer;
  width: 100%;
  text-align: center;
}

.submit-button:hover 
{
  background-color: #333333;
}

.forgot-password 
{
  color: #4b5563;
  font-size: 0.875rem;
  margin-top: 1rem;
  padding-bottom: 0.25rem;
  border-bottom: 2px solid #f3f4f6;
  text-decoration: none;
}

.overlay-container 
{
  position: absolute;
  top: 0;
  left: 60%;
  width: 40%;
  height: 100%;
  overflow: hidden;
  transition: transform 0.6s ease-in-out;
  z-index: 9;
}

.overlay-container.right-panel-active 
{
  transform: translateX(-150%);
}

.overlay-panel 
{
  position: relative;
  background: linear-gradient(to right, #1a1a1a, #1a1a1a);
  color: white;
  left: -150%;
  height: 100%;
  width: 250%;
  transition: transform 0.6s ease-in-out;
}

.overlay-panel.right-panel-active 
{
  transform: translateX(50%);
}

.overlay-left, .overlay-right 
{
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  text-align: center;
  height: 100%;
  padding: 0;
  box-sizing: border-box;
  width: 340px;
  transition: transform 0.6s ease-in-out;
}

.overlay-content 
{
  width: 80%;
  max-width: 280px;
  margin: 0 auto;
}

.overlay-left 
{
  right: 60%;
  transform: translateX(-12%);
}

.overlay-left.right-panel-active 
{
  transform: translateX(25%);
}

.overlay-right 
{
  right: 0;
}

.overlay-right.right-panel-active 
{
  transform: translateX(35%);
}

.overlay-title 
{
  color: white;
  font-size: 1.5rem;
  font-weight: 700;
  margin-bottom: 1rem;
  width: 100%;
  text-align: center;
}

.overlay-description 
{
  font-size: 0.875rem;
  font-weight: 300;
  line-height: 1.25rem;
  letter-spacing: 0.05em;
  margin-bottom: 2rem;
  width: 100%;
  text-align: center;
}

.overlay-button 
{
  padding: 0.75rem 2rem;
  background-color: transparent;
  border: 2px solid white;
  color: white;
  border-radius: 0.25rem;
  text-transform: uppercase;
  font-weight: 600;
  letter-spacing: 0.05em;
  transition: all 0.3s;
  cursor: pointer;
  width: 160px;
  display: inline-block;
  text-align: center;
}

.overlay-button:hover 
{
  background-color: white;
  color: #1a1a1a;
}

.sign-button 
{
  display: inline-block;
  padding: 0.75rem 2rem;
  background-color: #1a1a1a;
  color: white;
  border: 2px solid #1a1a1a;
  border-radius: 0.25rem;
  text-transform: uppercase;
  font-weight: 600;
  letter-spacing: 0.05em;
  transition: all 0.3s;
  cursor: pointer;
  width: 160px;
  text-align: center;
  text-decoration: none;
}

.sign-button:hover 
{
  background-color: #333333;
}

.sign-button.outline 
{
  background-color: transparent;
  border: 2px solid white;
  color: white;
}

.sign-button.outline:hover 
{
  background-color: white;
  color: #1a1a1a;
}
/* Mobile Responsiveness */
@media screen and (max-width: 900px) {
  .auth-card {
    width: 90%;
    max-width: 400px;
    height: auto;
    min-height: 600px;
    flex-direction: column;
  }

  .signup-form-container,
  .signin-form-container {
    width: 100%;
    height: auto;
    padding: 2rem 0;
    position: relative;
    transform: none;
    opacity: 1;
    z-index: 1;
  }

  .signup-form-container.right-panel-active,
  .signin-form-container.hidden {
    display: none;
  }

  .overlay-container {
    position: relative;
    width: 100%;
    height: 150px;
    left: 0;
    transform: none !important;
  }

  .overlay-panel {
    position: relative;
    width: 100%;
    left: 0;
    height: 100%;
    transform: none !important;
    background: linear-gradient(to bottom, #1a1a1a, #1a1a1a);
  }

  .overlay-left,
  .overlay-right {
    width: 100%;
    padding: 1rem;
    transform: none !important;
    position: absolute;
    right: auto;
  }

  .overlay-left {
    top: 0;
  }

  .overlay-right {
    top: 50%;
  }

  .signup-form,
  .signin-form {
    padding: 1.5rem;
  }

  .form-title {
    font-size: 1.3rem;
    margin-bottom: 1.5rem;
  }

  .input {
    padding: 0.6rem 1rem;
  }

  .submit-button {
    padding: 0.6rem 1.5rem;
    font-size: 0.9rem;
  }

  .overlay-button {
    padding: 0.6rem 1.5rem;
    font-size: 0.9rem;
    width: 140px;
  }
}

/* Very small devices (e.g., iPhone 5/SE) */
@media screen and (max-width: 375px) {
  .auth-card {
    width: 95%;
    min-height: 550px;
  }

  .signup-form,
  .signin-form {
    padding: 1rem;
  }

  .form-title {
    font-size: 1.2rem;
    margin-bottom: 1rem;
  }

  .input {
    padding: 0.5rem 0.8rem;
    font-size: 0.9rem;
  }

  .submit-button {
    padding: 0.5rem 1rem;
    font-size: 0.8rem;
  }

  .overlay-title {
    font-size: 1.2rem;
  }

  .overlay-description {
    font-size: 0.8rem;
    margin-bottom: 1rem;
  }

  .overlay-button {
    padding: 0.5rem 1rem;
    font-size: 0.8rem;
    width: 120px;
  }

  .popup-content {
    padding: 1.5rem;
  }

  .popup-icon {
    width: 80px;
    height: 80px;
  margin-bottom: 0.8rem;
  }

  .popup-message {
    font-size: 0.9rem;
  }
}

/* Landscape orientation for mobile */
@media screen and (max-width: 900px) and (orientation: landscape) {
  .auth-card {
    min-height: 450px;
  }

  .signup-form-container,
  .signin-form-container {
    padding: 1rem 0;
  }

  .overlay-container {
    height: 120px;
  }
}